export default {
	data() {
		return {
			title: '',
			artContent: ''
		}
	},
	onLoad: function(e) {
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				this.title = res.data.title;
				this.artContent = res.data.content;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {

	}
}
